before, q:after {\r\n  content: '';\r\n  content: none;\r\n}\r\n\r\ntable {\r\n  border-collapse: collapse;\r\n  border-spacing: 0;\r\n}\r\n\r\na {\r\n  color: inherit;\r\n  text-decoration: none;\r\n}\r\n\r\ns, u {\r\n  text-decoration: none;\r\n}\r\n\r\nselect {\r\n  appearance: none;\r\n  -webkit-appearance: none;\r\n}\r\n\r\ninput[type=\"submit\"],\r\nbutton {\r\n  width: auto;\r\n  overflow: visible;\r\n  cursor: pointer;\r\n  line-height: inherit;\r\n  color: inherit;\r\n\r\n  /* Corrects inability to style clickable `input` types in iOS */\r\n  appearance: none;\r\n}\r\n\r\nbutton::-moz-focus-inner {\r\n  /* Remove excess padding and border in Firefox 4+ */\r\n  border: 0;\r\n  padding: 0;\r\n}\r\n\r\n/* safari requires some special resets for input type=\"search\" */\r\ninput[type=\"search\"] {\r\n  -webkit-appearance: textfield;\r\n}\r\ninput[type=\"search\"]::-webkit-search-decoration {\r\n  -webkit-appearance: none;\r\n}\r\n\r\n/* ie 11 has it's own magic font-size rules for sub and sup */\r\n@media all and (-ms-high-contrast: none), (-ms-high-contrast: active) {\r\n  sub, sup {font-size:120%;}\r\n}\r\n\r\n/* some sensible global styles */\r\n:root {\r\n /* prevents mobile browsers from sometimes scaling text */\r\n  text-size-adjust: 100%;\r\n  -webkit-text-size-adjust: 100%;\r\n}\r\n\r\n/* sets box-sizing back to the sane border-box for all elements */\r\n*,\r\n*::before,\r\n*::after {\r\n  box-sizing: border-box;\r\n}\r\n","\r\n@mixin import_fonts( $font, $variants ){\r\n\t@each $variant in $variants{\r\n\t\t@font-face {\r\n\t\t\tfont-family: #{$font}-#{$variant};\r\n\t\t\tsrc: url(\"./assets/fonts/#{$font}/#{$font}-#{$variant}.ttf\");\r\n\t\t}\r\n\t}\r\n}\r\n\r\n@mixin position( $pos ){\r\n\t@if $pos == center {\r\n\t\tdisplay: flex;\r\n\t\tjustify-content: center;\r\n\t\talign-items: center;\r\n\t}@else if $pos == left {\r\n\t\tdisplay: flex;\r\n\t\tjustify-content: flex-start;\r\n\t\talign-items: center;\r\n\t}@else if $pos == right {\r\n\t\tdisplay: flex;\r\n\t\tjustify-content: flex-end;\r\n\t\talign-items: center;\r\n\t}\r\n}"," \n\t\t\t\t\t\t@use \"./src/globals/scss\" as globals;\n\t\t\t\t\t\n\r\n\r\n\t@include globals.import_fonts(\"Poppins\", (\r\n\t\t\"Regular\", \"Medium\", \"Bold\", \"Thin\", \"Light\"\r\n\t));\r\n\r\n\tbody{\r\n\t\tfont-family: \"Poppins-Regular\";\r\n\t\tfont-size: 16px;\r\n\t}\r\n\r\n"],"sourceRoot":""}]);
