ont, $variants ){\r\n\t@each $variant in $variants{\r\n\t\t@font-face {\r\n\t\t\tfont-family: #{$font}-#{$variant};\r\n\t\t\tsrc: url(\"./assets/fonts/#{$font}/#{$font}-#{$variant}.ttf\");\r\n\t\t}\r\n\t}\r\n}\r\n\r\n@mixin position( $pos ){\r\n\t@if $pos == center {\r\n\t\tdisplay: flex;\r\n\t\tjustify-content: center;\r\n\t\talign-items: center;\r\n\t}@else if $pos == left {\r\n\t\tdisplay: flex;\r\n\t\tjustify-content: flex-start;\r\n\t\talign-items: center;\r\n\t}@else if $pos == right {\r\n\t\tdisplay: flex;\r\n\t\tjustify-content: flex-end;\r\n\t\talign-items: center;\r\n\t}\r\n}"],"sourceRoot":""}]);
