vertical-align: baseline;\r\n}\r\n\r\n/* HTML5 display-role reset for older browsers */\r\narticle, aside, details, figcaption, figure, \r\nfooter, header, hgroup, menu, nav, section {\r\n  display: block;\r\n}\r\n\r\nbody {\r\n  /* line-height is set by :root */\r\n  line-height: inherit;\r\n}\r\n\r\nol, ul {\r\n  list-style: none;\r\n}\r\n\r\nblockquote, q {\r\n  quotes: none;\r\n}\r\n\r\nblockquote:before, blockquote:after,\r\nq:before, q:after {\r\n  content: '';\r\n  content: none;\r\n}\r\n\r\ntable {\r\n  border-collapse: collapse;\r\n  border-spacing: 0;\r\n}\r\n\r\na {\r\n  color: inherit;\r\n  text-decoration: none;\r\n}\r\n\r\ns, u {\r\n  text-decoration: none;\r\n}\r\n\r\nselect {\r\n  appearance: none;\r\n  -webkit-appearance: none;\r\n}\r\n\r\ninput[type=\"submit\"],\r\nbutton {\r\n  width: auto;\r\n  overflow: visible;\r\n  cursor: pointer;\r\n  line-height: inherit;\r\n  color: inherit;\r\n\r\n  /* Corrects inability to style clickable `input` types in iOS */\r\n  appearance: none;\r\n}\r\n\r\nbutton::-moz-focus-inner {\r\n  /* Remove excess padding and border in Firefox 4+ */\r\n  border: 0;\r\n  padding: 0;\r\n}\r\n\r\n/* safari requires some special resets for input type=\"search\" */\r\ninput[type=\"search\"] {\r\n  -webkit-appearance: textfield;\r\n}\r\ninput[type=\"search\"]::-webkit-search-decoration {\r\n  -webkit-appearance: none;\r\n}\r\n\r\n/* ie 11 has it's own magic font-size rules for sub and sup */\r\n@media all and (-ms-high-contrast: none), (-ms-high-contrast: active) {\r\n  sub, sup {font-size:120%;}\r\n}\r\n\r\n/* some sensible global styles */\r\n:root {\r\n /* prevents mobile browsers from sometimes scaling text */\r\n  text-size-adjust: 100%;\r\n  -webkit-text-size-adjust: 100%;\r\n}\r\n\r\n/* sets box-sizing back to the sane border-box for all elements */\r\n*,\r\n*::before,\r\n*::after {\r\n  box-sizing: border-box;\r\n}\r\n"," \n\t\t\t\t\t\t@use \"./src/globals/scss\" as globals;\n\t\t\t\t\t\n\r\n\r\n\t.button{\r\n\t\t// TYPOGRAPHY\r\n\t\tcolor : globals.$red;\r\n\t\tfont-size: 1.25rem;\r\n\t\tfont-family: \"Poppins-Bold\"; \r\n\t\ttransition: color .5s;\r\n\r\n\t\t// POSITIONING & SIZING\r\n\t\twidth: 200px;\r\n\t\theight: 50px;\r\n\t\tposition: relative;\r\n\t\t@include globals.position( center );\r\n\r\n\t\t// EXTRA\r\n\t\toverflow: hidden;\r\n\t\tborder: 3px solid globals.$red;\r\n\t\tborder-radius: 5px;\r\n\t\tbackground: rgba( 0, 0, 0, 0 );\r\n\r\n\t\t.btn_bg{\r\n\t\t\ttop: 0;\r\n\t\t\tleft: 0;\r\n\t\t\twidth: 100%;\r\n\t\t\theight: 100%;\r\n\t\t\tposition: absolute;\r\n\t\t\tbackground-color: globals.$red;\r\n\t\t\ttransition: transform .5s;\r\n\t\t\ttransform: translateX( -200px );\r\n\t\t}\r\n\r\n\t\t.btn_text{ position: relative; z-index: 1; }\r\n\r\n\t}\r\n\r\n\t.button:hover>.btn_bg{ transform: translateX( 0px ); }\r\n\t.button:hover{ color: rgb( 255, 255, 255 ); }\r\n\r\n","\r\n// COLORS\r\n$red : #8A0000;\r\n$blue : #00008A;\r\n$black : #191817;\r\n$light : #214756; // #1D575C\r\n$white : #ffffff;","\r\n@mixin import_fonts( $font, $variants ){\r\n\t@each $variant in $variants{\r\n\t\t@font-face {\r\n\t\t\tfont-family: #{$font}-#{$variant};\r\n\t\t\tsrc: url(\"./assets/fonts/#{$font}/#{$font}-#{$variant}.ttf\");\r\n\t\t}\r\n\t}\r\n}\r\n\r\n@mixin position( $pos ){\r\n\t@if $pos == center {\r\n\t\tdisplay: flex;\r\n\t\tjustify-content: center;\r\n\t\talign-items: center;\r\n\t}@else if $pos == left {\r\n\t\tdisplay: flex;\r\n\t\tjustify-content: flex-start;\r\n\t\talign-items: center;\r\n\t}@else if $pos == right {\r\n\t\tdisplay: flex;\r\n\t\tjustify-content: flex-end;\r\n\t\talign-items: center;\r\n\t}\r\n}"],"sourceRoot":""}]);
