<template>
	<a href="#" class="button">
		<div class="btn_bg"></div>
		<button>
			<div class="btn_text">
				{{ btnText }}
			</div>
		</button>
	</a>
</template>

<script>
	export default {
		props : {
			text : { type : String, default : "CLICK ME" } 
		},
		data(){ return { btnText : this.text, }}
	}
</script>

<style  lang="scss" scoped>

	.button{
		// TYPOGRAPHY
		color : globals.$red;
		font-size: 1.25rem;
		font-family: "Poppins-Bold"; 
		transition: color .5s;

		// POSITIONING & SIZING
		width: 200px;
		height: 50px;
		position: relative;
		@include globals.position( center );

		// EXTRA
		overflow: hidden;
		border: 3px solid globals.$red;
		border-radius: 5px;
		background: rgba( 0, 0, 0, 0 );

		.btn_bg{
			top: 0;
			left: 0;
			width: 100%;
			height: 100%;
			position: absolute;
			background-color: globals.$red;
			transition: transform .5s;
			transform: translateX( -200px );
		}

		.btn_text{ position: relative; z-index: 1; }

	}

	.button:hover>.btn_bg{ transform: translateX( 0px ); }
	.button:hover{ color: rgb( 255, 255, 255 ); }

</style>