<template>
	<div class="logo" :style="styleObj">
		<div class="text">LOMA.</div>
	</div>
</template>

<script>
	export default {
		props : {
			width : { type : Number, default : 120 },
			height : { type : Number, default : 50 },
			fontSize : { type : Number, default : 2 },
		},
		data(){ return {
			styleObj : {
				width : `${this.width}px`,
				height : `${this.height}px`,
				fontSize : `${this.fontSize}px`,
			}
		}}
	}
</script>

<style lang="scss" scoped>
	.logo{

		width: 110px;
		height: 49px;
		background: globals.$red;
			//linear-gradient( 90deg, globals.$blue 40%, globals.$red 60% );
		border-radius: 5px;
		@include globals.position( center );

		.text{
			font-size: 2rem;	
			font-family: "Poppins-Bold";
			color: rgb( 255, 255, 255 );
			text-rendering: optimizeLegibility;
		}
	}
</style>